body#visual-portal-wrapper.pat-plone #edit-zone .contenttype-frontpage::after {
  content: '';
}

body#visual-portal-wrapper.pat-plone .outer-wrapper .contenttype-frontpage::before {
  content: '';
}

body#visual-portal-wrapper.pat-plone #edit-zone .contenttype-teaser::after {
  content: '';
}

body#visual-portal-wrapper.pat-plone .outer-wrapper .contenttype-teaser::before {
  content: '';
}

#frontpage-carousel {
  margin-bottom: 30px;
}

.teaser-wrapper:before {
  display: block;
  float: left;
  width: 100%;
  content: '';
}

.teaser-wrapper {
  position: relative;
  display: block;
  float: left;
}

.teaser {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  float: left;
}

.teaser-text {
  position: absolute;
  right: 30px;
  bottom: 30px;
  left: 30px;
  color: #fff;
}

.teaser-image img {
  width: 100%;
  height: 100%;
}

#frontpage-text {
  margin-bottom: 30px;
}

#frontpage-news {
  margin-bottom: 30px;
}

.news-item {
    float: left;
    margin-bottom: 20px;
}

.news-title {
  margin-bottom: 15px;
  padding: 0 1px;
}

.news-description {
  padding: 0 1px;
}

.news-image {
  margin-bottom: 15px;
}

.news-image img {
  width: 100%;
  height: auto;
}

@media (max-width: 767px) {

    .teaser-description {
        display: none;
    }

}

@media (min-width: 768px) and (max-width: 991px) {

    .news-item {
        width: 350px;
        min-height: 360px;
        margin-right: 20px;
    }

    .news-item:nth-child(2n) {
        margin-right: 0;
    }

}

@media (min-width: 992px) and (max-width: 1199px) {

    .news-item {
        width: 460px;
        min-height: 360px;
        margin-right: 20px;
    }

    .news-item:nth-child(2n) {
        margin-right: 0;
    }

}

@media (min-width: 1200px) {

    .news-item {
        width: 366px;
        min-height: 360px;
        margin-right: 21px;
    }

    .news-item:nth-child(3n) {
        margin-right: 0;
    }

}

